@font-face {
  font-family: pradyse;
  src: url(/static/media/pradyse.18a7584d.otf);
}


.container {
  position: relative;
  width: 850px;
  height: 510px;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  -webkit-transition: background 0.5s ease;
  transition: background 0.5s ease;
}

.container:hover .overlay {
  display: block;
  background: rgba(0, 0, 0, .5);
}

img {
  position: absolute;
  width: 850px;
  height: 510px;
  box-shadow: 12px 29px 81px 0px rgba(0,0,0,0.75);
}

.separator {
  height: 500px;
}

.title {
  position: absolute;
  width: 500px;
  top: 150px;
  font-weight: 700;
  font-size: 30px;
  text-align: center;
  text-transform: uppercase;
  color: #efdab9;
  z-index: 1;
  -webkit-transition: top .5s ease;
  transition: top .5s ease;
  font-family: Alegreya Sans SC;
  font-weight: 100;
  font-size: 75px;
}

.container:hover .title {
  top: 20px;
}

.button {
  position: absolute;
  top: 210px;
  text-align: center;
  opacity: 0;
  -webkit-transition: opacity .35s ease;
  transition: opacity .35s ease;
  font-weight: 100;
  font-size: 25px;
  left: 25%;
}

.button a {
  width: 200px;
  padding: 12px 48px;
  border: solid 1px #efdab9;
  z-index: 1;
  text-decoration: none;
  color: #efdab9;
  font-family: Alegreya Sans SC;


}

p{
  text-align: right;
  color: #efdab9;
  font-family: Alegreya Sans SC;
  font-size: 30px;
  font-weight: 100;
}

.button-two {
  position: absolute;
  top: 210px;
  /* left: 190px;  */
  text-align: center;
  opacity: 0;
  -webkit-transition: opacity .35s ease;
  transition: opacity .35s ease;
  font-weight: 100;
  font-size: 25px;
  right: 25%;


}

.button-two a {
  width: 200px;
  padding: 12px 55px;
  border: solid 1px #efdab9;
  z-index: 1;
  text-decoration: none;
  color: #efdab9;
  font-family: Alegreya Sans SC;


}
.container:hover .button {
  opacity: 1;
}

.container:hover .button-two {
  opacity: 1;
}

